* {
    margin: 0px;
    padding: 0px;
    border-radius: border-box;
}
.heading{
    color: black;
}
body {
    font-family: "Lato", sans-serif;
}
.pricing-table {
    padding: 40px 0px;
    color: blue;
}
.pricing-table
.details {
    width: 90%;
    max-width: 500%;
    text-align: center;
    margin: 0 auto;
    padding: 40px 20px;
}
.pricing-table
.details  h2 {
    font-size: 30px;
    margin-bottom: 10px;
}
.pricing-table
.details p {
    font-size: 15px;
}
.pricing-table .grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px 0;
    padding: 20px;
}
.pricing-table .grid .box {
    width: 250px;
    border: 1px solid #eee;
    text-align: center;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 2px 10px 5px rgba(0,0,0,0.05);
}
.pricing-table .grid .box .title {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 20px;
    color:#555;

}
.pricing-table .grid .box .price {
    margin-bottom: 20px;
}
.pricing-table .grid .box .price  b {
    display: block;
    font-size: 40px;
    margin-bottom: -5px;
}
.pricing-table .grid .box .features  > * {
    color: #555;
    padding: 8px 0px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
}
.pricing-table .grid .box .button  button {
    width: 100%;
    margin: 25px 0px 0px;
    padding: 10px;
    background: linear-gradient(to bottom, #3a7bd5 , #3a6073);
    color: #fff;
    border-radius: 5px;
    outline: none;
    border: none;
    font-weight: 600;
    cursor: pointer;

}
.pricing-table .grid .box.prfessional {
    transform: scale(1.1);
    background: linear-gradient(to bottom, #3a7bd5 , #3a6073); 
}
.pricing-table .grid .box .prfessional .title {
    color: #eee;

}
.pricing-table .grid .box .prfessional .price {
    color: #fff;
}
.pricing-table .grid .box .prfessional .features > * {
    color: #fff;
    border-bottom: 1px solid rgba(255,255,255,0.5);

}
.pricing-table .grid .box .prfessional .button  button {
    background: #fff;
    color: #3a6073;
}
@media (max-width:804px){
    .pricing-table .grid {
        gap: 20px
    }
    .pricing-table .grid .box .prfessional {
        transform: scale(1);
    }
}


.btn {
    background-color: green;
    color: white;
    padding: 0rem;
    margin-bottom: 20px;
    border-radius: 50px;
    font-weight: 600;
    font-size: 1.5rem;
    justify-self: center;
    text-align: center;

}
.id {
    font-family: sans-serif, "Leto";
    font-weight: 600;
    justify-content: center;
    text-align: center;
    margin-bottom: 2.1rem;

}
.adhar {
    font-size: 1.5rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    text-align: center;
}
.vefification {
    display: flex;
    justify-content: center;
    text-align: center;
}
.input-text {
    color: black;
    margin-bottom: 2rem;
    padding: 0.5% ;
}

.button{
    display: flex;
    justify-content: center;
    align-items: center;
}